/* ###### Bootstrap ###### */
/* ###### Accordion ###### */
.accordion-color .card-header a.collapsed {
  color: rgba(255, 255, 255, 0.8) !important;
  font-weight: 500;
  letter-spacing: -0.1px;
}

.accordion-color .card-header a.collapsed:hover,
.accordion-color .card-header a.collapsed:focus {
  color: #ffffff;
}

.accordion-faq .accordion .card-header a {
  background-color: #f5f5fd;
}

.accordion .card {
  border-width: 0;
}

.accordion .card + .card {
  margin-top: 1px;
}

.accordion .card-header {
  padding: 0;
  background-color: transparent;
  font-weight: 500;
}

.accordion .card-header a {
  display: block;
  padding: 14px 20px;
  position: relative;
  font-weight: 500;
  font-size: 14px;
  background-color: #e1e1f9;
}

.accordion .card-header a.collapsed {
  color: #3c4858;
  background-color: #e1e1f9;
  border-bottom-color: transparent;
}

.accordion .card-header a.collapsed:hover,
.accordion .card-header a.collapsed:focus {
  color: #8f9cc0;
  background-color: #eaedf7;
  background-color: #f5f5fd;
}

.accordion .card-body {
  padding: 15px;
  background-color: #ffffff;
  transition: none;
  border: 1px solid #e8e8f7;
  border-bottom: 0;
}

.accordion > .card {
  overflow: hidden;
}

.accordion > .card:not(:first-of-type) .card-header:first-child {
  border-radius: 0;
}

.accordion > .card:not(:first-of-type):not(:last-of-type) {
  border-bottom: 0;
  border-radius: 0;
}

.accordion > .card:first-of-type {
  border-bottom: 0;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.accordion > .card:last-of-type {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.accordion > .card .card-header {
  margin-bottom: -1px;
}

.collapse:not(.show) {
  display: none;
}

.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition: height 0.35s ease;
}

@media (prefers-reduced-motion: reduce) {
  .collapsing {
    transition: none;
  }
}

.accordion .card-header a {
  background-color: #e1e1f9;
}

