  background-size: 66%;
  background-repeat: no-repeat;
  background-position: 55% 45%;
  line-height: 17px;
  display: none;
  border-radius: 3px;
}

.ckbox input[type="checkbox"] {
  opacity: 0;
  margin: 0;
}

